import React from 'react'
export const Footer= () => {
  let footerStyle= {
    position: "relative", 
    top: "70vh",
    minHeight: "100%",

  }
 

  return (
    
    <footer className="bg-dark text-light py-3" 
    style={footerStyle}>
      <p className="text-center" >
        Copyright &copy; Mytodolist.com@shivalika</p>
        </footer>
  )
}
